body {
  background-color: #fff7f0;
}

.container {
  margin: 0 16px;
}

@media screen and (min-width: 768px) {
  .container {
    margin: 0 40px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    margin: 0 165px;
  }
}
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.4;
  font-size: 18px;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 500;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img,
picture,
svg {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.home__content {
  padding: 16px 0px 32px 0px;
}
.home .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
}
.home .header__logo {
  width: 48px;
  height: 48px;
}
.home .header__button {
  cursor: pointer;
  border-radius: 28px;
  border: none;
  color: #fff7f0;
  font-size: 14px;
  font-weight: 700;
  outline: none;
  padding: 14px 28px;
  background-color: #030303;
}
.home .header__button:hover,
.home .header :active {
  background-color: #755cde;
  transition-duration: 0.3s;
}
.home__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.home__heading {
  font-size: 36px;
  color: #030303;
  line-height: 45px;
  margin-bottom: 16px;
}
.home__text {
  font-size: 16px;
  padding: 0px 25px;
  line-height: 26px;
}

@media screen and (min-width: 768px) {
  .home .header {
    margin-bottom: 64px;
  }
  .home .header__logo {
    width: 64px;
    height: 64px;
  }
  .home .header__button {
    padding: 15px 45px;
  }
  .home__content {
    padding: 34px 0px 32px 0px;
  }
  .home__heading {
    font-size: 44px;
    margin-bottom: 27px;
  }
  .home__text {
    font-size: 18px;
    padding: 0 45px;
  }
}
@media screen and (min-width: 1440px) {
  .home .header {
    margin-bottom: 66px;
  }
  .home__title {
    margin-inline: auto;
  }
  .home__content {
    padding: 34px 0px 40px 0px;
  }
  .home__heading {
    font-size: 56px;
  }
  .home__text {
    width: clamp(780px, 75%, 1200px);
  }
}
.about__content {
  padding: 32px 0px;
}
.about__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}
.about__box:not(:last-child) {
  margin-bottom: 24px;
}
.about__desing {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 24px;
  border-radius: 8px;
  align-items: center;
  height: 364px;
  background-color: #755cde;
}
.about__image {
  margin-left: auto;
}
.about__heading {
  margin-right: auto;
  color: #fff7f0;
}
.about__minibox {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  width: 100%;
}
.about__card {
  display: flex;
  align-items: center;
  gap: 24px;
  width: 100%;
}
.about__ui {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 24px;
  border-radius: 8px;
  height: 182px;
  background-color: #f6a560;
}
.about__apps {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 24px;
  border-radius: 8px;
  height: 182px;
  background-color: #f39e9e;
}
.about__illustration {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 24px;
  border-radius: 8px;
  height: 158px;
  background-color: #eb7565;
}
.about__photography {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 24px;
  border-radius: 8px;
  height: 182px;
  background-color: #61c4b7;
}
.about__mgraphics {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 24px;
  border-radius: 8px;
  height: 182px;
  background-color: #552049;
}

@media screen and (min-width: 768px) {
  .about__content {
    padding: 32px 0px 64px 0px;
  }
  .about__box--first {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .about__box--second {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .about__minibox {
    row-gap: 24px;
  }
}
@media screen and (min-width: 1440px) {
  .about__content {
    display: grid;
    grid-template-columns: 2fr 1fr;
    padding: 40px 0px 64px 0px;
    gap: 24px;
  }
  .about__box--first {
    margin-bottom: 0px;
  }
  .about__box--second {
    grid-template-columns: auto;
  }
  .about__box:not(:last-child) {
    margin-bottom: 0px;
  }
  .about__desing {
    height: 388px;
  }
  .about__card {
    height: 183px;
  }
  .about__illustration {
    height: 183px;
  }
}
.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 68px 0px 100px 0px;
}
.contact__content {
  display: flex;
  text-align: center;
  gap: 40px;
}
.contact__image {
  width: 300px;
  height: 300px;
  margin-bottom: 40px;
}
.contact__description {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}
.contact__heading {
  font-size: 26px;
}
.contact__text {
  font-size: 16px;
  min-width: 343px;
}
.contact__button {
  cursor: pointer;
  border-radius: 28px;
  border: none;
  color: #fff7f0;
  font-size: 14px;
  font-weight: 700;
  outline: none;
  padding: 14px 28px;
  background-color: #eb7565;
}
.contact__button:hover {
  background-color: #f6a560;
}

@media screen and (min-width: 768px) {
  .contact {
    justify-content: center;
  }
  .contact__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0;
  }
  .contact__image {
    width: 364px;
    height: 364px;
    position: absolute;
    left: -44px;
    margin-bottom: 0px;
  }
  .contact__description {
    grid-column-start: 2;
    align-items: flex-start;
    text-align: left;
    gap: 32px;
  }
  .contact__heading {
    font-size: 32px;
  }
  .contact__text {
    font-size: 18px;
    line-height: 28px;
  }
  .contact__button {
    padding: 14px 45px;
  }
}
@media screen and (min-width: 1440px) {
  .contact {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .contact__content {
    grid-template-columns: 1fr;
  }
  .contact__image {
    position: unset;
    width: 445px;
    height: 445px;
  }
  .contact__heading {
    font-size: 40px;
  }
}
.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}
.slider__content {
  position: relative;
  max-width: 275px;
  height: 180px;
}
.slider__slides {
  display: none;
}
.slider__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
}
.slider__button:hover {
  background-color: #755cde;
  transition: 0.5s;
}
.slider__button {
  display: block;
  background-color: #030303;
  border: none;
  cursor: pointer;
  width: 64px;
  height: 64px;
  border-radius: 100px;
}
.slider__icon {
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 768px) {
  .slider__content {
    max-width: 540px;
    height: 360px;
  }
}
.footer__content {
  display: flex;
  flex-direction: column;
  gap: 42px;
  padding: 100px 0px 32px 0px;
}
.footer__description {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 48px 24px;
  gap: 24px;
  border-radius: 8px;
  background-color: #030303;
}
.footer__title {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.footer__heading {
  font-size: 26px;
  color: #fff7f0;
}
.footer__text {
  font-size: 16px;
  color: #fff7f0;
}
.footer__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__button {
  cursor: pointer;
  border-radius: 28px;
  border: none;
  color: #fff7f0;
  font-size: 14px;
  font-weight: 700;
  outline: none;
}
.footer__button--description {
  padding: 14px 28px;
  color: #fff7f0;
  background-color: #eb7565;
}
.footer__button--box {
  padding: 14px 28px;
  color: #fff7f0;
  background-color: #030303;
}
.footer__button--description:hover {
  background-color: #f6a560;
}
.footer__button--box:hover {
  background-color: #755cde;
}
.footer__logo {
  width: 48px;
  height: 48px;
}

@media screen and (min-width: 768px) {
  .footer__description {
    padding: 56px 75px 64px 75px;
    gap: 26px;
  }
  .footer__heading {
    font-size: 32px;
  }
  .footer__text {
    font-size: 18px;
  }
  .footer__button--description {
    padding: 14px 45px;
  }
  .footer__logo {
    width: 64px;
    height: 64px;
  }
  .footer__button--box {
    padding: 14px 45px;
  }
}
@media screen and (min-width: 1440px) {
  .footer__description {
    display: grid;
    justify-content: space-between;
    grid-template-columns: 3fr 209px;
    padding: 81px 95px 80px 65px;
    gap: 82px;
  }
  .footer__title {
    text-align: left;
  }
  .footer__heading {
    font-size: 40px;
  }
  .footer__text {
    width: clamp(540px, 80% + 100px, 1000px);
  }
}